// Bootstrap and its default variables
// @import "~bootstrap/scss/bootstrap"

#root {
	padding: $globalPaddingY 0;
	min-height: 100vh; }

.proportional {
	@include aspect-ratio-wrapper(1, 1.4); }

.proportional-inner {
	@include aspect-ratio-container(); }

.proportional-square {
	@include aspect-ratio(1, 1); }

.img-centered {
	position: relative;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%); }

// for some reason the CSS file node-sass compiles is not the same as the source SASS
// the output will be like this:
// background-position: 100% calc(.375em + .1875rem);
.form-control.is-invalid, .was-validated .form-control:invalid,
.form-control.is-valid, .was-validated .form-control:valid {
	// background-position: center right calc(0.375em + 0.1875rem)
	background-position: calc(100% - (.375em + .1875rem)) 50%; }
